body {
  font-family: 'Lato', sans-serif;
  font-family: 'Poppins', sans-serif;
  margin: 0;
  padding: 0;
}
a {
  text-decoration: none;
  color: #99aade;
}
.flex {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
i {
  color: #4c77eb;
}
.center {
  text-align: center;
}
header {
  background-image: url(../img/header-img.png);
  background-repeat: no-repeat;
}
.header2 {
  background-image: url(../img/about-head.png);
  background-repeat: no-repeat;
}
.container {
  margin: 0 300px;
  margin-bottom: 100px;
}
.header-main {
  padding: 40px;
}
.header-main button {
  margin-left: 20px;
}
.head-button button {
  font-weight: 700;
  font-size: 16px;
  color: white;
  text-decoration: none;
  padding: 20px 40px;
  outline: none;
  border-width: 2px 0;
  border-style: solid none;
  border-color: #7492ec;
  border-radius: 30px;
  background: #7492ec;
  transition: 0.2s;
}
.head-button {
  color: #fff;
  font-size: 12px;
  font-weight: 700;
}
.menu p {
  margin-left: 30px;
  color: #99aade;
  font-size: 16px;
  font-weight: 700;
}
.video {
  width: 560px;
  height: 315px;
}
.header-down {
  text-align: left;
  margin-top: 100px;
  margin-left: 1180px;
}
.header-down h2 {
  color: #4c77eb;
  font-size: 72px;
  font-weight: 300;
}
.about-us-left {
  margin-top: 500px;
}
.about-us-left h2 {
  color: #7492ec;
  font-size: 48px;
  font-weight: 300;
}
.about-us-left p {
  color: #666666;
  font-size: 14px;
  font-weight: 400;
}
.about-us-left iframe {
  margin-top: 30px;
}
.about-us-right {
  margin-left: 250px;
  margin-top: 250px;
}
.about-us-right h3 {
  color: #000000;
  font-size: 18px;
  font-weight: 900;
}
.about-us-right p {
  color: #666666;
  font-size: 14px;
  font-weight: 400;
}
.about-us-right img {
  margin-right: 40px;
}
.our-classes h2 {
  color: #7492ec;
  font-size: 48px;
  font-weight: 400;
}
.our-classes-flex {
  display: flex;
  justify-content: flex-start;
  align-items: center;
}
.our-classes-flex p {
  margin-left: 10px;
}
.our-classes-card {
  text-align: center;
  margin-right: 40px;
}
.our-classes-card h3 {
  color: #7492ec;
  font-size: 18px;
  font-weight: 900;
}
.our-classes-card p {
  color: #999999;
  font-size: 13px;
  font-weight: 400;
}
.our-classes-card h4 {
  margin-bottom: 40px;
  color: #666666;
  font-size: 14px;
  font-weight: 500;
}
.gallery {
  text-align: center;
}
.gallery h2 {
  color: #000000;
  font-size: 32px;
  font-weight: 400;
}
.gallery p {
  color: #666666;
  font-size: 14px;
  font-weight: 700;
}
.gallery button {
  margin-top: 20px;
}
.gallery-img {
  margin-top: 20px;
}
.gallery img {
  margin: 10px;
}
.meet {
  text-align: center;
  color: #fff;
  background-image: url(../img/meet.png);
  background-repeat: no-repeat;
  padding: 40px;
}
.meet h2 {
  font-size: 32px;
  font-weight: 700;
}
.meet p {
  font-size: 14px;
  font-weight: 400;
}
.meet h3 {
  margin-top: 40px;
  font-size: 14px;
  font-weight: 400;
}
.meet i {
  margin-top: 10px;
  font-size: 40px;
  color: #fff;
}
.team {
  margin-top: 40px;
}
.meet-card {
  margin-top: 60px;
}
.meet-text {
  margin-left: 20px;
  text-align: left;
}
.meet-text h3 {
  font-size: 16px;
  font-weight: 900;
  color: #000000;
}
.meet-text p {
  font-size: 14px;
  font-weight: 400;
  color: #999999;
}
.series {
  padding: 300px 300px;
  background-image: url(../img/series.png);
  background-repeat: no-repeat;
}
.series-text h2 {
  font-size: 48px;
  font-weight: 700;
  color: #7492ec;
}
.series-text p {
  font-size: 21px;
  font-weight: 400;
  color: #8ea2dc;
}
.footer {
  display: flex;
  flex-flow: row wrap;
  padding: 30px 30px 20px 30px;
  color: #2f2f2f;
  background-color: #fff;
  border-top: 1px solid #e5e5e5;
}
.footer > * {
  flex: 1 100%;
}
.footer__addr {
  margin-right: 1.25em;
  margin-bottom: 2em;
}
.footer__logo {
  font-family: 'Pacifico', cursive;
  font-weight: 400;
  text-transform: lowercase;
  font-size: 1.5rem;
}
.footer__addr h2 {
  margin-top: 1.3em;
  font-size: 15px;
  font-weight: 400;
}
.nav__title {
  font-weight: 400;
  font-size: 15px;
}
.footer address {
  font-style: normal;
  color: #999;
}
.footer__btn {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 36px;
  max-width: max-content;
  background-color: #4c77eb;
  border-radius: 100px;
  color: #2f2f2f;
  line-height: 0;
  margin: 0.6em 0;
  font-size: 1rem;
  padding: 0 1.3em;
}
.footer ul {
  list-style: none;
  padding-left: 0;
}
.footer li {
  line-height: 2em;
}
.footer a {
  text-decoration: none;
}
.footer__nav {
  display: flex;
  flex-flow: row wrap;
}
.footer__nav > * {
  flex: 1 50%;
  margin-right: 1.25em;
}
.nav__ul a {
  color: #999;
}
.nav__ul--extra {
  column-count: 2;
  column-gap: 1.25em;
}
.heart {
  color: #2f2f2f;
}
@media screen and (min-width: 24.375em) {
  .legal .legal__links {
    margin-left: auto;
  }
}
@media screen and (min-width: 40.375em) {
  .footer__nav > * {
    flex: 1;
  }
  .nav__item--extra {
    flex-grow: 2;
  }
  .footer__addr {
    flex: 1 0px;
  }
  .footer__nav {
    flex: 2 0px;
  }
}
.head-down-about {
  margin-top: 60px;
  margin-left: 200px;
}
.head-down-about h2 {
  font-size: 72px;
  font-weight: 400px;
  color: #4c77eb;
}
.head-down-about p {
  font-size: 14px;
  font-weight: 700px;
  color: #99aade;
}
.about-us2card {
  color: #666666;
  font-size: 14px;
  font-weight: 400px;
}
.about3-card {
  text-align: center;
  padding: 100px 0px;
  background-image: url(../img/about-3card.png);
  background-repeat: no-repeat;
}
.about3-card h3 {
  color: #fff;
  font-size: 24px;
  font-weight: 900px;
}
.about3-card p {
  color: #fff;
  font-size: 14px;
  font-weight: 900px;
}
.about4-card {
  margin-top: 100px;
}
.about4-card h3 {
  color: #7492ec;
  font-size: 18px;
  font-weight: 900px;
}
.about4-card p {
  color: #666666;
  font-size: 14px;
  font-weight: 400px;
}
.about4-flex {
  margin-right: 40px;
}
.about5-card {
  background-color: #e5eaf7;
  padding: 100px 0;
  text-align: center;
}
.about5-card-up,
.about5-card-down h2 {
  color: #7492ec;
  font-size: 16px;
  font-weight: 900px;
}
.about5-card-up,
.about5-card-down p {
  color: #99aade;
  font-size: 16px;
  font-weight: 400px;
}
.about5-card-down p {
  color: #99aade;
  font-size: 12px;
  font-weight: 400px;
}
.about5-card-number {
  margin-top: 40px;
}
.about5-card-number h3 {
  color: #7492ec;
  font-size: 42px;
  font-weight: 400px;
}
.about5-card-number p {
  color: #99aade;
  font-size: 14px;
  font-weight: 400px;
}
.about6-card {
  padding: 100px;
  text-align: center;
}
.about6-card h2 {
  color: #000000;
  font-size: 32px;
  font-weight: 400px;
}
.about6-card p {
  color: #666666;
  font-size: 14px;
  font-weight: 700px;
}
.pricing {
  margin-top: 200px;
}
.head-button-pricing button {
  margin-top: 40px;
  font-weight: 700;
  font-size: 12px;
  color: white;
  text-decoration: none;
  padding: 20px 40px;
  outline: none;
  border-width: 2px 0;
  border-style: solid none;
  border-color: #7492ec;
  border-radius: 30px;
  background: #7492ec;
  transition: 0.2s;
}
.pricing-card-up {
  display: flex;
  justify-content: flex-start;
  align-items: center;
}
.pricing-card-up h2 {
  color: #7492ec;
  font-size: 10px;
  font-weight: 900px;
}
.pricing-card-up p {
  color: #999999;
  font-size: 14px;
  font-weight: 700px;
}
.pricing h2 {
  color: #7492ec;
  font-size: 32px;
  font-weight: 700px;
}
.pricing p {
  color: #666666;
  font-size: 14px;
  font-weight: 700px;
}
.contact {
  margin-top: 100px;
}
.postcard {
  max-width: 350px;
  margin: 50px auto 0;
  padding: 20px;
  border: 10px solid transparent;
  font-family: 'Playfair Display';
  color: #605756;
}
.form-row {
  margin-bottom: 15px;
}
.form-row input[type="text"],
.form-row input[type="email"],
.form-row textarea {
  width: 100%;
  padding: 0 10px;
  line-height: 35px;
  border-width: 0;
  outline: none;
  border: 1px solid transparent;
  border-color: #4d68ff59;
  background-size: 8px 35px;
  font-family: 'Playfair Display';
}
.form-row textarea {
  background-attachment: local;
  background-repeat: repeat;
  resize: none;
}
.form-row input[type="submit"] {
  padding: 10px 20px;
  margin-top: 10px;
  background: #4d68ff;
  border-width: 0;
  cursor: pointer;
  outline: none;
  font-family: 'Playfair Display';
  font-size: 13px;
  letter-spacing: 1px;
  color: #fffdee;
  text-transform: uppercase;
  transition: 1s cubic-bezier(0.165, 0.84, 0.44, 1);
}
.form-row input[type=submit]:hover {
  background: #011067;
}
@media screen and (max-width: 900px) {
  .flex {
    flex-wrap: wrap;
  }
  img {
    max-width: 350px;
  }
  .container {
    margin: 0 50px;
  }
  .header-down {
    text-align: center;
    margin-left: 20px;
  }
  .head-button {
    margin-top: 30px;
  }
  .header-down h2 {
    font-size: 60px;
  }
  .about-us-left {
    margin-top: 300px;
  }
  .about-us-left h2 {
    width: 100px;
  }
  .about-us-left p {
    width: 300px;
  }
  .video {
    width: 260px;
    height: 160px;
  }
  .about-us-right {
    margin-left: 0px;
  }
  .our-classes {
    margin-top: 100px;
  }
  .gallery img {
    width: 250px;
  }
  .meet {
    padding: 10px 40px;
  }
  .series {
    padding: 40px;
  }
  .head-down-about {
    margin: 0px;
    padding: 40px;
  }
  .about3-card h3 {
    margin: 0 auto;
    width: 250px;
  }
}
@media screen and (min-width: 900px) and (max-width: 1500px) {
  .container {
    margin: 0 200px;
  }
  .flex {
    flex-wrap: wrap;
  }
  .about-us-right {
    margin-left: 0px;
  }
  .gallery {
    text-align: left;
  }
}
